@mixin font($fname, $fontweight, $fnamefull, $fontstyle) {
	@font-face {
		font-family: $fname;
		font-style: $fontstyle;
		font-weight: $fontweight;
		src: url('/fonts/dist/'+ $fnamefull +'.eot') format("eot");
		src: url('/fonts/dist/'+ $fnamefull +'.svg') format("svg");
		src: url('/fonts/dist/'+ $fnamefull +'.ttf') format("ttf");
		src: url('/fonts/dist/'+ $fnamefull +'.woff') format("woff");
		src: url('/fonts/dist/'+ $fnamefull +'.woff2') format("woff2");
	}
}

@include font('fontawesome-webfont', normal, 'fontawesome-webfont', normal);
@include font('Montserrat-Bold', normal, 'Montserrat-Bold', normal);
@include font('Montserrat-Medium', normal, 'Montserrat-Medium', normal);
@include font('Montserrat-SemiBold', normal, 'Montserrat-SemiBold', normal);

$font_awesome: 'fontawesome-webfont';
$font_default: "Montserrat-Medium";
$font_semibold: "Montserrat-SemiBold";
$font_bold: "Montserrat-Bold";