*,*:after,*:before{
	box-sizing: border-box;
}
li{
	list-style: none;
}
.page nav ul li{
	margin-bottom: 10px;
}
.example nav ul li:hover a{
	color: blue;
}
.page{
	padding-top: 100px;
}
.page .container{
	background: #f6f6f6;
	padding: 40px;
}
.no-header{
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 30px;
	background: #0ca76e;
}
.no-header a{
	padding: 15px;
	background: #00784b;
	color: #f6f6f6;
}

.container-h{
	display: flex;
	justify-content: space-between;
}
.custom-h{
	text-align: right;
}
.select_section ul{
	list-style: none;
	margin: 20px 0;
}
.label_checkbox{
	margin-top: 30px;
}
.custom_input{
	top: 30px;
}
.input_body .custom_textarea{
	margin-top: 80px;
}
.scrollbar_body .text{
	width: 400px;
	height: 200px;
	margin: 0 auto;
	border: 1px black solid;
}

.header-1 .container ul{
	display: flex;
	justify-content: space-between;
	align-items: center;
	text-align: center;
	height: 100%;
	width: 100%;
	padding: 0 20px 0 0;
}
.header-1 header{
	height: 100px;
	background: #0ca76e;
}
.header-1 .container{
	height: 100%;
	display: flex;
	align-items: center;
}
.header-1 .container > ul > li{
	background: #197052;
	padding: 15px;
}
.header-1 .container ul li{
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.header-1 .container ul ul{
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	background: #007af1;
	position: absolute;
	top: 23px;
	min-width: 150px;
	height: 0;
	overflow: hidden;
	-webkit-transition: 0.5s;
	-moz-transition: 0.5s;
	-ms-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
	z-index: 1000;
}
.header-1 .container ul ul{
	top: 37px;
	background: #006942;
}
.header-1 .container ul li.show ul{
	height: 200px;
	padding: 20px;
}
.header-1 .time-work{
	display: none;
}

/* HEADER 2 LINE */

.header-2 .rows .container,
.header-2 .rows .container ul{
	display: flex;
	text-align: center;
	align-items: center;
	justify-content: space-between;
}
.header-2 .rows .container ul{
	width: 100%;
}
.header-2 .rows{
	padding: 15px 0;
}
.header-2 .row-1{
	background: #00784b;
}
.header-2 .row-2{
	background: #007aff;
}
.header-2 .row-2 .container ul li{
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.header-2 .row-2 .container ul ul{
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	background: #007af1;
	position: absolute;
	top: 23px;
	min-width: 150px;
	height: 0;
	overflow: hidden;
	-webkit-transition: 0.5s;
	-moz-transition: 0.5s;
	-ms-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
	z-index: 1000;
}
.header-2 .row-2 .container ul li.show ul{
	height: 200px;
	padding: 20px;
}

/* HEADER 3 LINE */

.header-3 .row-2 .container ul li.show ul{
	height: 200px;
	padding: 20px;
}
.header-3 .row-2 .container ul ul{
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	background: #007af1;
	position: absolute;
	top: 23px;
	min-width: 150px;
	height: 0;
	overflow: hidden;
	-webkit-transition: 0.5s;
	-moz-transition: 0.5s;
	-ms-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
	z-index: 1000;
}
.header-3{
	margin-left: 100px;
}
.header-3 header{
	padding-left: 100px;
}
.header-3 .page p{
	padding-top: 50px;
}
.header-3 header .row-1{
	background: #00784b;
}
.header-3 header .row-1 .container{
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 15px;
}
.header-3 header .row-2{
	background: #0ca76e;
}
.header-3 header .row-2 .container{
	position: sticky;
	top: 0;
}
.header-3 header .row-2 .container>nav>ul{
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: 15px 0;
}
.header-3 header .row-2 .header-sidebar>nav{
	width: 100px;
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	overflow: hidden;
	background: #007aff;
	z-index: 5;
	-webkit-transition: 0.5s;
	-moz-transition: 0.5s;
	-ms-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}
.header-3 header .row-2 .header-sidebar>nav>ul{
	width: 350px;
}
.header-3 header .row-2 .header-sidebar>nav>ul li{
	padding: 20px 20px 20px 100px;
	border-bottom: 1px #2b2b2b solid;
	position: relative;
	display: flex;
	align-items: center;
	cursor: pointer;
}
.header-3 header .row-2 .header-sidebar>nav>ul li:before{
	content: "";
	width: 15px;
	height: 15px;
	background: #2b2b2b;
	position: absolute;
	left: 40px;
}
.header-3 header .row-2 .header-sidebar>nav>ul li a{
	font-size: 35px;
}
.header-3 header .row-2 .header-sidebar>nav:hover{
	width: 350px;
}
.header-3 header .row-2 .header-sidebar nav ul li.open-nav{
	display: none;
}

@media (max-width: 1200px) /* Для сайбара слева */ {
	.header-3 .page{
		padding-top: 75px;
	}
	.header-3 header .row-2 .header-sidebar nav ul li.open-nav{
		display: flex;
	}
	.header-3 .container{
		width: 100%;
	}
	.header-3 .logo,
	.header-3 .time-work{
		display: flex;
	}
	.phone-1{
		padding-right: 20px;
		font-size: 22px;
	}
	header .mobile-menu{
		display: flex;
	}
	.header-3{
		margin: 0;
	}
	.header-3 header{
		padding: 0;
	}
	.header-3 header .header-sidebar .container,
	.header-3 header .row-2 .header-sidebar>nav{
		width: 350px;
		left: -350px;
	}
	.header-3 header.show-menu .header-sidebar.open .container,
	.header-3 header.show-menu .row-2 .header-sidebar>nav{
		left: 0;
	}
	.header-3 header .row-2 .header-sidebar>nav>ul li{
		padding-left: 65px;
	}
	.header-3 header .row-2 .header-sidebar>nav>ul li:before{
		display: none;
	}
	.header-3 header .header-sidebar .background{
		-webkit-transition: 0.5s;
		-moz-transition: 0.5s;
		-ms-transition: 0.5s;
		-o-transition: 0.5s;
		transition: 0.5s;
		left: 0;
	}
	.header-3 header.show-menu .header-sidebar .background{
		position: fixed;
		top: 0;
		left: 350px;
		bottom: 0;
		right: 0;
		width: 100%;
		height: 100%;
		background: #f6f6f699;
	}
	.header-3 header .header-sidebar .container{
		margin: 0;
		padding: 0;
		position: fixed;
		-webkit-transition: 0.5s;
		-moz-transition: 0.5s;
		-ms-transition: 0.5s;
		-o-transition: 0.5s;
		transition: 0.5s;
		background: #007aff;
		top: 0;
		bottom: 0;
		z-index: 25;
	}
	.header-3 header .row-2 .container>nav>ul{
		flex-direction: column;
		align-items: flex-start;
		margin: 0;
	}
	.header-3 header .row-2 .container>nav>ul>li{
		padding: 20px 20px 20px 65px;
		border-bottom: 1px #2b2b2b solid;
		position: relative;
		display: flex;
		align-items: flex-start;
		cursor: pointer;
		width: 100%;
	}
	.header-3 header .row-2 .container>nav>ul>li a{
		text-align: left;
	}
	.header-3 header .row-2 .container>nav>ul>li a{
		font-size: 35px;
	}
	.header-3 .row-2 .container ul ul{
		top: 65px;
	}
	.header-3 header .row-2 .header-sidebar>nav>ul{
		width: 100%;
	}
}

@media (max-width: 1100px) /*горизонтальный планешт*/  {
	header .mobile-menu{
		display: flex;
	}
	.phone-1{
		padding-right: 20px;
		font-size: 22px;
	}
	.header-1 .container{
		justify-content: space-between;
	}
	.header-1 .container>ul{
		padding: 20px;
		position: fixed;
		flex-direction: column;
		height: 600px;
		width: 400px;
		top: 0;
		left: -400px;
		background: #f6f6f6;
		-webkit-transition: left 0.5s;
		-moz-transition: left 0.5s;
		-ms-transition: left 0.5s;
		-o-transition: left 0.5s;
		transition: left 0.5s;
	}
	.header-1 header.show-menu .container>ul{
		left: 0;
	}
	.header-1 .logo,
	.header-1 .time-work{
		display: flex;
	}
	.header-2 .time-work{
		display: none;
	}
	.header-2 .row-2{
		position: fixed;
		width: 400px;
		min-height: 700px;
		max-height: 700px;
		overflow: auto;
		top: 0;
		left: -400px;
		-webkit-transition: left 0.5s;
		-moz-transition: left 0.5s;
		-ms-transition: left 0.5s;
		-o-transition: left 0.5s;
		transition: left 0.5s;
	}
	.header-2 header.show-menu .row-2{
		left: 0;
	}
	.header-2 .row-2 .container{
		height: 100%;
		padding: 15px 40px;
	}
	.header-2 .row-2 ul{
		flex-direction: column;
		height: 100%;
	}
}

@media (max-width: 768px) /*вертикальный планешт*/ {
	.header-1 .container>ul,
	.header-2 .row-2{
		width: 100%;
		height: 100vh;
		max-height: 100vh;
		left: 0;
		top: -120%;
		bottom: -120%;
		-webkit-transition: top 0.5s;
		-moz-transition: top 0.5s;
		-ms-transition: top 0.5s;
		-o-transition: top 0.5s;
		transition: top 0.5s;
	}
	.header-1 header.show-menu .container>ul,
	.header-2 header.show-menu .row-2{
		bottom: 0;
		top: 0;
	}
	.header-1 .time-work{
		display: none;
	}
	.header-3 .header-sidebar .background{
		display: none;
	}
	.header-3 header .header-sidebar .container,
	.header-3 header .row-2 .header-sidebar>nav{
		width: 100%;
		left: -100%;
	}
	.header-3 header .row-2 .header-sidebar>nav:hover{
		width: 100%;
	}
}

@media (max-width: 576px) /*телефоны*/ {
	.phones-box,
	.time-work{
		display: none;
	}
	.header-3 .time-work{
		display: none;
	}
	.header-3 header.show-menu .header-sidebar>nav{
		width: 100%;
	}
	header .mobile-menu{
		margin: 9px 0 8px;
	}
}

.test-form{
	width: 600px;
	margin: 0 auto;
}
.test-form form.sendler{
	padding: 30px;
	border: 1px #939393 solid;
	margin-top: 40px;
}