.checkbox {
  vertical-align: top;
  margin: 0;
  width: 0;
  height: 0;
  opacity: 0;
}
.checkbox:not(checked) {
  position: absolute;
  opacity: 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  -webkit-filter: alpha(opacity=0);
  -moz-filter: alpha(opacity=0);
  -o-filter: alpha(opacity=0);
  filter: alpha(opacity=0);
}
.checkbox:not(checked) + label {
  position: relative;
  padding: 0 0 0 40px;
  font-size: 18px;
  color: #000;
  font-family: "Montserrat-Medium";
  cursor: pointer;
  line-height: 18px;
  display: flex;
  align-items: center;
}
.checkbox:not(checked) + label p, .checkbox:not(checked) + label span {
  color: #000;
  font-family: "Montserrat-Medium";
  font-size: 16px;
  line-height: 1.1;
}
.checkbox:not(checked) + label span {
  cursor: pointer;
  text-decoration: underline;
}
.checkbox:not(checked) + label:after {
  content: "";
  position: absolute;
  left: 0;
  width: 25px;
  height: 25px;
  background: transparent;
  -webkit-transition: none;
  -moz-transition: none;
  -o-transition: none;
  transition: none;
  border: 1px solid #000;
}
.checkbox:checked + label:after {
  content: "\f00c";
  font-size: 23px;
  line-height: 1;
  font-family: "fontawesome-webfont";
  cursor: pointer;
  color: #000;
}

