/*	todo: pagination	*/
.pagination {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 60px;
}
.pagination li {
  display: inline-block;
  margin: 0 11px;
  user-select: none;
  font-size: 18px;
  line-height: 1;
}
.pagination li a, .pagination li span {
  font-size: 18px;
  line-height: 1;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  padding: 0 5px;
}
.pagination li.active span,
.pagination li a:hover,
.pagination li span:hover {
  color: #ea5718;
}
.pagination li.active {
  position: relative;
}
.pagination li.active span {
  display: block;
  text-align: center;
  width: 20px;
}
.pagination li.active:before {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  border: 1px #ea5718 solid;
  border-radius: 15px;
  left: -4px;
  top: -7px;
}
.pagination li.prev a, .pagination li.next a {
  font-size: 14px;
  font-family: "Montserrat-SemiBold";
  position: relative;
  display: flex;
  align-items: center;
  width: auto;
  padding-left: 20px;
}
.pagination li.prev a:before, .pagination li.prev a:after, .pagination li.next a:before, .pagination li.next a:after {
  content: "";
  position: absolute;
  left: 0;
  width: 8px;
  height: 10px;
  background: url("../images/paginaciya.svg") no-repeat left top;
  background-size: 18px;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.pagination li.prev a:after, .pagination li.next a:after {
  background-position-y: -11px;
  opacity: 0;
}
.pagination li.prev a:hover:before, .pagination li.next a:hover:before {
  opacity: 0;
}
.pagination li.prev a:hover:after, .pagination li.next a:hover:after {
  opacity: 1;
}
.pagination li.next a {
  padding-left: 0;
  padding-right: 20px;
}
.pagination li.next a:after, .pagination li.next a:before {
  left: auto;
  right: 0;
  background-position-x: -11px;
}
.pagination li.first {
  margin-right: 0;
  white-space: nowrap;
}
.pagination li.first:after {
  content: "...";
  font-size: 18px;
  font-family: "Montserrat-Medium";
  margin-left: 5px;
}
.pagination li.last {
  margin-left: 0;
  white-space: nowrap;
}
.pagination li.last:before {
  content: "...";
  font-size: 18px;
  font-family: "Montserrat-Medium";
  margin-right: 5px;
}

