  0% {
    right: -110%;
  }
  100% {
    right: 0;
  }
}
.float-box_inset {
  width: 50%;
  position: absolute;
  right: 0;
  top: 0;
  background: #fff;
  border: 40px #fff solid;
  border-top: 55px #fff solid;
  border-right: 20px #fff solid;
  -webkit-animation: floatbox_inset 0.7s ease-in-out;
  -moz-animation: floatbox_inset 0.7s ease-in-out;
  -o-animation: floatbox_inset 0.7s ease-in-out;
  animation: floatbox_inset 0.7s ease-in-out;
  overflow: auto;
  overflow-x: hidden;
  display: block;
  height: 100%;
  padding-right: 10px;
  -webkit-transition: right 0.5s;
  -moz-transition: right 0.5s;
  -o-transition: right 0.5s;
  transition: right 0.5s;
}
.float-box_inset::-webkit-scrollbar,
.float-box_inset .scrollbar::-webkit-scrollbar {
  width: 8px;
}
.float-box_inset::-webkit-scrollbar-button,
.float-box_inset .scrollbar::-webkit-scrollbar-button {
  width: 0;
  height: 0;
}
.float-box_inset::-webkit-scrollbar-track,
.float-box_inset .scrollbar::-webkit-scrollbar-track {
  background-color: transparent;
  width: 5px;
  border-right: 3px #000 solid;
}
.float-box_inset::-webkit-scrollbar-track-piece,
.float-box_inset .scrollbar::-webkit-scrollbar-track-piece {
  background-color: transparent;
}
.float-box_inset::-webkit-scrollbar-thumb,
