  background-color: #00784b;
  width: 8px;
}
.float-box_inset::-webkit-scrollbar-corner,
.float-box_inset .scrollbar::-webkit-scrollbar-corner {
  background-color: transparent;
}
.float-box_inset::-webkit-resizer,
.float-box_inset .scrollbar::-webkit-resizer {
  background-color: transparent;
}
.float-box_inset > *:first-child,
.float-box_inset .scrollbar > *:first-child {
  margin-top: 0;
}

